.ReportPage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}

.reportButton {
    display: flex;
    margin: auto;
    padding: 15px 35px;
    width: 100%;
    font-size: 18pt;
    align-items: center;
    gap: 10px;
}

.reportButton:hover {
    opacity: 0.9;
}

.reportButton:active {
    opacity: 0.8;
}

.emailButton {
    color: #ffffff;
    background: #707070;
}

.githubButton {
    color: #ffffff;
    background: #000000;
}